import React from 'react'
import Affair from './Affair'
import {AffairType, FilterType} from './HW2'
import s from './Affairs.module.css'


type AffairsPropsType = { // need to fix any
    data: AffairType[]
    setFilter: (setFilter: FilterType) => void
    deleteAffairCallback: (id: number) => void
}

function Affairs(props: AffairsPropsType) {
    const mappedAffairs = props.data.map((a: AffairType) => (
        <Affair // should work
            key={a._id} //    99% -           
            affair={a}
            deleteAffairCallback={props.deleteAffairCallback}
        />

    ))

    const setAll = () => {
        props.setFilter('all')
    } // need to fix
    const setHigh = () => {
        props.setFilter('high')
    }
    const setMiddle = () => {
        props.setFilter('middle')
    }
    const setLow = () => {
        props.setFilter('low')
    }

    return (
        <div>
            {mappedAffairs}
            <div className={s.btn_block}>
                <button onClick={setAll} className={s.btn}>All</button>
                <button onClick={setHigh} className={s.btn}>High</button>
                <button onClick={setMiddle} className={s.btn}>Middle</button>
                <button onClick={setLow} className={s.btn}>Low</button>
            </div>
        </div>
    )
}

export default Affairs
